/*eslint-disable*/
import React, { Component, Suspense, lazy } from "react";
import { HashRouter, Route, Redirect, NavLink, Switch } from "react-router-dom";
import { Row, Col } from "antd";
import "./index.less";
import "antd/dist/antd.css";

const Popular = lazy(() => import("./popular/popular"));
const Battle = lazy(() => import("./battle/battle"));
const Result = lazy(() => import("./battle/result"));

class App extends Component {
  render() {
    return (
      <div className="popular">
        <HashRouter>
          <Row>
            <Col xs={{ span: 12 }} lg={{ span: 6 }}>
              <ul className="popular-ul">
                <li>
                  <NavLink
                    to="/popular"
                    activeClassName="active"
                    onClick={this.clickEvt}
                  >
                    Popular
                  </NavLink>
                </li>
                <li>
                  <NavLink
                    to="/battle"
                    activeClassName="active"
                    onClick={this.clickEvt}
                  >
                    Battle
                  </NavLink>
                </li>
              </ul>
            </Col>
          </Row>
          <Suspense fallback={<div>Loading...</div>}>
            <Switch>
              <Route path="/popular" component={Popular} />
              <Route path="/battle" component={Battle} exact />
              <Route path="/battle/result" component={Result} />
              <Redirect from="/" to="/popular/all" exact />
            </Switch>
          </Suspense>
        </HashRouter>
      </div>
    );
  }
}

export default App;
